<template><undefined>
<h1>Message 全局提示</h1>
<p>全局展示操作反馈信息。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>可提供成功、警告和错误等反馈信息。</li>
<li>顶部居中显示并自动消失，是一种不打断用户操作的轻量级提示方式。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;button type=\&quot;button\&quot; class=\&quot;ant-btn ant-btn-primary\&quot; @click=\&quot;openMessage\&quot;&gt;\n        &lt;span&gt;显示普通提醒&lt;/span&gt;\n    &lt;/button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        methods: {\n            openMessage() {\n                this.$message.info(\&quot;这是一条普通的提醒\&quot;);\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <button type="button" class="ant-btn ant-btn-primary" @click="openMessage">
        <span>显示普通提醒</span>
    </button>
</template>

</div>
