<h3 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h3>
<ul>
<li><code v-pre="">this.$message.success(content, duration)</code></li>
<li><code v-pre="">this.$message.error(content, duration)</code></li>
<li><code v-pre="">this.$message.info(content, duration)</code></li>
<li><code v-pre="">this.$message.warning(content, duration)</code></li>
<li><code v-pre="">this.$message.loading(content, duration)</code></li>
</ul>
<h3 id="props"><a class="header-anchor" href="#props" aria-hidden="true">¶</a> Props</h3>
<p>组件提供了四个静态方法，参数如下：</p>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>提示内容</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>duration</td>
<td>自动关闭的延时，单位秒</td>
<td>number</td>
<td>1.5</td>
</tr>
</tbody>
</table>
<p>还提供了一个全局配置方法，在调用前提前配置，全局一次生效。</p>
<ul>
<li><code v-pre="">this.$message.config(options)</code></li>
<li><code v-pre="">this.$message.destroy()</code></li>
</ul>
<pre v-pre=""><code v-pre="" class="hljs language-js"><span class="hljs-keyword">this</span>.$message.config({
  <span class="hljs-attr">top</span>: <span class="hljs-number">100</span>,
  <span class="hljs-attr">duration</span>: <span class="hljs-number">2</span>,
});
</code></pre>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>top</td>
<td>消息距离顶部的位置</td>
<td>number</td>
<td>24px</td>
</tr>
<tr>
<td>duration</td>
<td>默认自动关闭延时，单位秒</td>
<td>number</td>
<td>1.5</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
    export default {
        methods: {
            openMessage() {
                this.$message.info("这是一条普通的提醒");
            },
            openTypeMessage(type) {
                this.$message[type]("这是一条类型的提醒");
            },
            openMessageCustomizeDuration() {
                this.$message.info("这是一条自定义duration的提醒", 10);
            },
            openLoadingMessage() {
                const hide = this.$message.loading("这是一条加载中的提醒");
                setTimeout(hide, 2500);
            }
        }
    }
</script>


